.myTitle h1 {
  font-size: 3rem;
  text-align: center;
  letter-spacing: 0.1rem;
}

.myTitle p {
  font-size: 1rem;
  text-align: center;
  letter-spacing: 0.09rem;
}

.inputSection {
  height: 60%;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.input {
  max-width: 400px;
  margin-left: 20px;
  display: flex;
  align-items: center;
}

input {
  height: 30px;
  width: 300px;
  border: 1px solid white;
  padding-left: 10px;
  font-size: 1rem;
  outline: none;
}

input::placeholder {
  opacity: 0.7;
  padding-left: 10px;
}

input:focus {
  border: 4px solid green;
}

.input label {
  width: 30%;
  font-size: 1.2rem;
}

.Add {
  display: flex;
  justify-content: center;
}

.Add button {
  height: 40px;
  font-size: 1rem;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 40px;

  background-color: rgb(205, 120, 50, 0.8);
}

.Add button:hover {
  background-color: rgb(205, 120, 50);
  transform: scale(1.1);
}

.allsection {
  position: relative;
  margin-top: 20px;
  height: 570px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.inputSection p {
  text-align: center;
  font-size: 1rem;
}

.classSection {
  height: 58%;
  width: 100%;
}
.classSection p {
  text-align: center;
  font-size: 0.8rem;
}

.class {
  display: flex;
  width: 90%;
  margin: auto;
  justify-content: space-between;
  align-items: center;
}

.class button.del {
  padding: 6px 18px;
  background-color: rgba(194, 26, 26, 0.795);
  color: white;
  outline: none;
  border: none;
  border-radius: 30px;
}

.class button.del:hover {
  background-color: red;
}
.class button:hover {
  transform: scale(1.1);
}

.class button {
  padding: 14px 30px;
  color: white;
  border: none;
  border-radius: 10px;
  background-color: rgba(7, 3, 0, 0.692);
}

.class button.active {
  background-color: green;
}

.class p {
  font-size: 1.2rem;
  font-weight: 900;
}

.allsection.active {
  filter: blur(1.6px);
}
.allsection.active + .message {
  display: block;
  height: 60px;
  width: 200px;
  padding: 20px;
  display: grid;
  place-content: center;
  border-radius: 20px;
  background-color: white;
}

.remain {
  text-align: center;
  font-size: 20px;
  margin-top: -50px;
}

.message {
  position: absolute;
  display: none;
  top: 50%;
  font-size: 1.2rem;
  left: 50%;
  transform: translate(-50%, 50%);
}

@media only screen and (min-width: 700px) {
  .allsection {
    margin-top: 40px;
    max-width: 900px;
    margin: auto;
    display: flex;
    flex-direction: row;
  }

  .classSection {
    width: 40%;
  }
}
